.card {
    float: left;
    height: 115px;
    padding: 12px 10px;
    -webkit-box-shadow: 2px 3px 5px grey;
            box-shadow: 2px 3px 5px grey;
    margin: 15px;
    margin-left: 25px;
}

.card:hover {
    -webkit-box-shadow: 6px 9px 5px grey;
            box-shadow: 6px 9px 5px grey;
}

.image {
    width: 100px;
    height: 100px;
    margin: auto;
}

#red {
    -webkit-animation: flash 1.5s forwards;
            animation: flash 1.5s forwards;
}

@-webkit-keyframes flash {
    from {
        background: red;
    }
    to {
        background: white;
    }
}

@keyframes flash {
    from {
        background: red;
    }
    to {
        background: white;
    }
}


